import React, { Component } from 'react';
import DocsContainer from './containers/DocsContainer'
import RuneFinderContainer from './containers/RuneFinderContainer'
import GestureFinderContainer from './containers/GestureFinderContainer'
import { BrowserRouter, Link, Route, Switch } from "react-router-dom";

class App extends Component {
  render() {
    return (
      <BrowserRouter basename="/bloodborne-ally-app">
        <div>
          <div id="wrapper">
            <nav
              className="container navbar navbar-inverse navbar-fixed-top bg-light border-bottom"
              role="navigation"
            >
              <h1>Bloodborne Ally</h1>
              <Link to={process.env.PUBLIC_URL}>Home</Link>
              <Link to={process.env.PUBLIC_URL+"/rune"}>Rune Search</Link>
              <Link to={process.env.PUBLIC_URL+"/gesture"}>Gesture Search</Link>
            </nav>
            <div className="container bg-light pt-3" style={{minHeight: "100vh"}}>
              <Switch>
                <Route path={process.env.PUBLIC_URL+"/gesture"} component={GestureFinderContainer} />
                <Route path={process.env.PUBLIC_URL+"/rune"} component={RuneFinderContainer} />
                <Route path={process.env.PUBLIC_URL+"/"} component={DocsContainer} />
              </Switch>
            </div>
          </div>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;
